import { useState, useEffect } from "react"
import { inject, observer } from 'mobx-react';
import Card from "../../pages/Pool/Card"
import apiProxy from '../../model/ApiProxy'
import selectIcon from '../../assets/img/arrow-down.svg'
import '../../pages/Pool/pool.less'
import './retired.less'
function Retired({ lang, wallet = {}, poolManager }) {
  const [curTabPool, setCurTabPool] = useState('amm');
  const [curOpen, setCurOpen] = useState(false);
  const [poolTitle, setPoolTitle] = useState(lang['deri-pool-title'])
  const [nowPool, setNowPool] = useState([]);
  const [checkedStaked, setCheckedStaked] = useState(false)
  const [stakedAmmList, setStakedAmmList] = useState([])
  const [stakedLpList, setStakedLpList] = useState([])


  const getStakedPools = async () => {
    let filterPools = [...poolManager.ammPools, ...poolManager.lpPools];
    let arr = await Promise.all(filterPools.map(async (item) => {
      let params = [item.chainId, item.address, wallet.detail.account]
      let res = await apiProxy.request('getUserStakeInfo', params)
      if (res.isStaked) {
        return item
      } else {
        return false
      }
    }))
    arr = arr.filter(p => p)
    let ammArr = arr.filter(p => p.type !== 'lp')
    let lpArr = arr.filter(p => p.type === 'lp')
    setStakedAmmList(ammArr)
    setStakedLpList(lpArr)
  }


  useEffect(() => {
    if (wallet.isConnected()) {
      getStakedPools()
    }
  }, [wallet.detail, poolManager, curTabPool])

  useEffect(() => {
    if (curOpen) {
      setPoolTitle(lang['open-pool-title'])
    } else {
      setPoolTitle(lang['deri-pool-title'])
    }
  }, [curOpen])
  useEffect(() => {
    poolManager.load()
  }, [])

  useEffect(() => {
    setNowPool(poolManager.retiredPools)
  }, [poolManager.pools])
  return (
    <div className='pool-box'>
      <div className='pool-info'>
        <div className='left'>
          <div className='left-header-info'>
            <div className='pool-title'>
              <div className='pool-title-text'>
                {poolTitle}
              </div>
              <div className='pool-title-select-text'>
                {!curOpen && <>
                  <div className='pool-test-des'>
                    {lang['pool-title-des-one']}
                  </div>
                  <div className='pool-test-des'>
                    {lang['pool-title-des-two']}
                  </div>
                </>}
                {curOpen && <>
                  <div className='pool-test-des'>
                    {lang['open-pool-title-des-one']}
                  </div>
                  <div className='pool-test-des'>
                    {lang['open-pool-title-des-two']}
                  </div>
                </>}
              </div>

            </div>
          </div>
          <div className='pool-card-claim-bg'>
            <div className='retired-hint'>
              <div>Several V3 ( <a rel="noreferrer" target="_blank" href="https://deri.io/#/retired/trade/futures">Fut Main</a> ) and V2 markets ( <a rel="noreferrer" target="_blank" href="https://deri.io/#/retired/trade/options">Options</a> , <a rel="noreferrer" target="_blank" href="https://deri.io/#/retired/trade/futures">Fut Main &amp; Fut Inno-BUSD </a> pools) are no longer supported, please close position and remove your collaterals.</div>
            </div>

            <div className='pool-card'>
              {nowPool.map((item, index) => <Card wallet={wallet} lang={lang} pool={item} key={index} />)}
            </div>
            <div className></div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default inject('wallet', 'poolManager')(observer(Retired))