import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom';
import DeriNumberFormat from "../../utils/DeriNumberFormat"
import Button from '../../components/Button/Button'
import BSC from '../../assets/img/BNB.svg'
import ARBITRUM from '../../assets/img/arbitrum-logo.svg'
import POLYGON from '../../assets/img/polygon-logo.svg'
import ETH from '../../assets/img/ethereum.svg'
import stakedLogo from './img/staked.svg'
import errorimg from '../../assets/img/ErrorImg.png'
import classNames from 'classnames'
import close from './img/close.svg'
import './card.less'
import styled from 'styled-components';
export default function Card({ lang, pool, wallet }) {
  const history = useHistory();
  // const [textLink,setTextLink] = useState('')
  const [poolInfo, setPoolinfo] = useState({})
  const [isStaked, setIsStaked] = useState(false)
  const [xvsNum, setXvsNum] = useState()
  const [show, setShow] = useState(true)
  const getPoolInfo = async () => {
    setPoolinfo({})
    let res = await pool.getPoolInfo(pool.address, pool.chainId)
    setPoolinfo(res)
  }
  const getVenusEarned = async () => {
    if (pool && (pool.version === 'v3' || pool.version === 'v3_lite')) {
      let res = await pool.getVenusEarned(wallet, pool.address)
      setXvsNum(res)
    }
  }
  const getIsStaked = async () => {
    let res = await pool.getIsStaked(wallet, pool)
    if (res) {
      setIsStaked(res.isStaked)
    }

  }
  const onImgError = (event) => {
    event.target.src = errorimg
  }
  const closeSymbol = (address) => {
    sessionStorage.setItem(address, false)
    setShow(false)
  }
  useEffect(() => {
    getPoolInfo()
    if (wallet.isConnected()) {
      getIsStaked()
      getVenusEarned()
    }
  }, [pool, wallet.detail.account])
  useEffect(() => {
    let sess = sessionStorage.getItem(pool.address)
    if (sess !== null) {
      if (sess) {
        setShow(false)
      }
    }
  }, [pool])
  const networkLogo = (chainId) => {
    let src;
    if (chainId === "56" || chainId === "97") {
      src = BSC
    } else if (chainId === "137") {
      src = POLYGON
    } else if (chainId === "1") {
      src = ETH
    } else if (chainId === '42161' || chainId === '421611') {
      src = ARBITRUM
    }
    return src
  }
  const staking = (url) => {
    if (pool.version === 'v2_lite_open') {
      return
    }
    history.push(url)
  }
  const url = `/pool/${pool.version || 'v1'}/${pool.chainId}/${pool.type}/${pool.address}`
  return (
    <div className={`${pool.address === '0x243681B8Cd79E3823fF574e07B2378B8Ab292c1E' ? 'showGuide' : ''} card-box`}>
      <div className={pool.version === 'v2_lite_open' ? 'card-list open-list' : 'card-list'} onClick={() => { staking(url) }} >
        <div className={`card-header ${pool.retired ? pool.version : ""} ${pool.new ? "new" : ''}`}>
          <div className='pool-network-type'>
            <div className='network-logo'>
              <img alt='' src={networkLogo(pool.chainId)} />
            </div>
            <div className='pool-type-network-text'>
              <div className='pool-type-text'>
                {pool.address !== '0xD2D950e338478eF7FeB092F840920B3482FcaC40' && <>
                  {lang[pool.type]} {lang[pool.zone]}
                </>}
                {pool.address === '0xD2D950e338478eF7FeB092F840920B3482FcaC40' && <>
                  {lang[pool.zone]}
                </>}
              </div>
              <div className='pool-network-text'>
                {pool.chain.toUpperCase() === 'BSC' ? "BNB Chain" : pool.chain.toUpperCase()}
              </div>
            </div>
          </div>
          <div className='base-token-logo'>
            {pool.isLp && <>
              <div className='lp-base-token-bg'>
                <div className={`base-token-bg ${pool.bTokens[0].bTokenSymbol}`}></div>
                <span className='lp-con'>+</span>
                <div className={`base-token-bg ${pool.bTokens[1].bTokenSymbol}`}></div>
              </div>
            </>}
            {!pool.isLp && pool.version !== 'v3' && pool.version !== "v3_lite" && <>
              {pool.bTokens.map((item, index) => <div key={index} className='base-token-box-bg'>
                <div className={`base-token-bg ${item.bTokenSymbol}`}>
                  {pool.version === 'v2_lite_open' && <>
                    <img alt='' onError={onImgError} src={`https://raw.githubusercontent.com/deri-finance/deri-open-zone/main/img/${item.bTokenSymbol}.png`} />
                  </>}
                </div>
              </div>)}
            </>}
            {(pool.version === "v3" || pool.version === "v3_lite") &&
              <TokenExplode bTokens={pool.bTokens} />
            }
          </div>
        </div>
        <div className='card-content'>

          <div className='max-apy'>
            {pool.version !== 'v2_lite_open' && <>
              <div className='max-apy-title'>
                {(pool.version === 'v2' || pool.version === "v3_lite" || pool.version === "v3") ? lang['max-apy'] : lang['apy']}
              </div>
              <div className='max-apy-num'>
                <DeriNumberFormat value={poolInfo.apy} suffix='%' displayType='text' allowZero={true} decimalScale={2} />
              </div>
            </>}
          </div>
          <div className='total-pool-liquidity'>
            <div className='total-liquidity-title'>
              {lang['total-pool-liquidity']}
            </div>
            <div className='total-liquidity-num'>
              <DeriNumberFormat value={poolInfo.liquidity} displayType='text' thousandSeparator={true} decimalScale={pool.type === 'lp' ? 7 : 0} />
            </div>
          </div>
          <div className='your-deri-epoch'>
            {isStaked && <div className='staked-box'>
              <img src={stakedLogo} alt='' />
              {lang['staked']}
            </div>}
            {(+xvsNum > 0) && (pool.version === 'v3' || pool.version === 'v3_lite') && <span className='xvs-collectable'><div className='staked-box'><DeriNumberFormat width='50px' value={xvsNum} displayType='text' thousandSeparator={true} decimalScale={2} /> &nbsp; XVS  {lang['collectable']}</div></span>}
          </div>
          <div className='stake-button'>
            <Button disabled={pool.version === 'v2_lite_open' ? true : false} className='button' outline label={pool.version === 'v2_lite_open' ? lang['operating'] : lang['stake']} />
          </div>
        </div>
      </div>
      {/* {pool.version === "v3_lite" && show &&
        <div className='new-symbol'>
          <div className='new-symbol-text'>
            New Listing : <a rel="noreferrer" href='https://deri.io/#/trade/futures/FITFIUSDT' target="_blank" >FITFIUSDT</a> <a href='https://deri.io/#/trade/futures/AVAXUSDT' rel="noreferrer" target="_blank">AVAXUSDT</a>
          </div>
          <div className='close-symbol'>
            <img src={close} alt='close' onClick={() => closeSymbol(pool.address)} />
          </div>
        </div>
      } */}
      
    </div>

  )
}

const TokenWrapper = styled.div`
  @-webkit-keyframes explode {
    0% {
        transform: translateX(0px);
        -moz-transform : translateX(0px);
    }
    100% {
        transform: translateX(100px);
        -moz-transform : translateX(100px);
    }
  }

  @keyframes explode {
    0% {
        transform: translateX(0px);
        -moz-transform : translateX(0px);
    }
    100% {
        transform: translateX(100px);
        -moz-transform : translateX(100px);
    }
  }
  .base-token{
    margin-left: 0px!important;
    z-index : -2;
    visibility: hidden;
    background-color: #111e34;
    border : 1px solid #fff;
  }
  @media only screen and (max-width: 760px) {
    .base-token{
      display:none;
    }
  }
  .base-token-box-bg.explode:hover .move  {
    -webkit-animation : explode .4s .0s linear;
    -moz-animation :  explode .4s .0s linear;
    animation: explode .4s .0s linear;
    // transform : translateX(100px)!important;
    visibility: visible;    
  }
  
  .base-token-box-bg.explode:hover .base-token {
    z-index : 2!important;
  } 

`
const Token = styled.div`
  position: absolute;
  transform : ${props => `rotate(${-props.rotate}deg)`};
  width: 48px;
  height: 48px;
  display: inline-block;
  border-radius: 50%;
  .move {
    transform : translateX(100px) ${props => `rotate(${props.rotate}deg) !important`};
  }
`

const TokenExplode = ({ bTokens = [] }) => {
  const angle = 360 / bTokens.length
  return (
    <TokenWrapper className='v3-tokens'>
      <div className='tokens'>
        {bTokens.filter((t, i) => i < 3).map((token, index) => (
          <div className='base-token-box-bg' key={index}>
            <div className={`base-token-bg ${token.bTokenSymbol}`}>
            </div>
          </div>
        ))}
      </div>
      <div className='base-token-box-bg explode'>
        <div className={'base-token-bg'}>
          {bTokens.length - 3} +
        </div>
        {bTokens.map((token, index) => {
          const curRotate = angle * index
          return (
            <Token className='base-token' rotate={curRotate} width='100' key={index}>
              <div width='100' rotate={curRotate} className={`base-token-bg move ${token.bTokenSymbol}`} >
              </div>
            </Token>
          )
        })}
      </div>
    </TokenWrapper>
  )
}